.header {
  position: absolute;
  z-index: 5;
  top: 30px;
  left: 0;
  width: 100%;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    background: #EAEAEA;
    border-radius: 20px;
    padding: 30px;
  }
  &__logo {
    width: 171px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__btn {
    height: 55px;
    border-radius: 174px;
    padding: 5px 40px;
    span {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    top: 20px;
    &__inner {
      padding: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 100px;
    }
    &__btn {
      height: 44px;
      padding: 5px 20px;
      span {
        font-size: 14px;
        letter-spacing: -0.2px;
      }
    }
  }
}