h2 {
  font-size: 40px;
  line-height: 150%;
  font-weight: 500;
  letter-spacing: -0.8px;
  .gradient-text {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      z-index: -1;
      top: 50%;
      left: -7px;
      transform: translate(0, -50%);
      width: 100%;
      height: 100%;
      background: linear-gradient(90.00deg, rgb(99, 129, 238),rgba(99, 129, 238, 0) 100%);
      opacity: 0.36;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -10px;
      transform: translate(0, -50%);
      width: 7px;
      height: 100%;
      background-image: url("../img/line.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      pointer-events: none;
    }
  }
}
.btn-black {
  gap: 8px;
  background: #222222;
  &:hover {
    background: #000;
  }
  span {
    line-height: 150%;
    color: #fff;
  }
  .arrow {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #EAEAEA;
    border-radius: 50%;
    svg {
      max-width: 90%;
      width: auto;
      height: auto;
    }
  }
}
.percent {
  position: relative;
  z-index: 2;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  padding: 9px 18px;
  line-height: 105%;
  color: #666666;
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 28px;
    letter-spacing: -0.6px;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 24px;
    letter-spacing: -0.2px;
  }
}