.main {
  background: linear-gradient(180.00deg, rgb(80, 114, 238),rgb(58, 96, 234) 100%);
  border-radius: 0 0 50px 50px;
  padding: 180px 0 120px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 40px;
  }
  &__left {
    position: relative;
    z-index: 3;
    width: 625px;
  }
  &__right {
    position: relative;
    width: calc(100% - 665px);
    padding-top: 30px;
  }
  &__title {
    font-size: 62px;
    line-height: 150%;
    font-weight: 500;
    letter-spacing: -1.24px;
    color: #fff;
  }
  &__items {
    max-width: 550px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
    gap: 8px;
    margin-top: 44px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 8px;
    &-icon {
      width: 40px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 160%;
      color: #CDD7FA;
    }
  }
  &__btn {
    height: 70px;
    gap: 26px;
    border-radius: 220px;
    padding: 5px 10px 5px 40px;
    margin-top: 60px;
    span {
      font-size: 20px;
      letter-spacing: -0.4px;
    }
    .arrow {
      width: 47px;
      height: 47px;
    }
  }
  &__text {
    position: relative;
    z-index: 3;
    max-width: 490px;
    font-size: 16px;
    line-height: 210%;
    color: #CDD7FA;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: -80px;
    width: 670px;
    &:after {
      content: '';
      position: absolute;
      top: 45%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 435px;
      height: 435px;
      background: #fff;
      border-radius: 50%;
      filter: blur(347px);
      opacity: 0.78;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__percent {
    position: absolute;
    left: -80px;
    bottom: 100px;
    transform: rotate(-21deg);
    border-radius: 10px;
    font-size: 17px;
    letter-spacing: -0.34px;
  }
  &__percent2 {
    position: absolute;
    top: 20px;
    right: -60px;
    transform: rotate(26deg);
    border-radius: 12px;
    font-size: 21px;
    letter-spacing: -0.42px;
  }
}
.analysis {
  padding: 100px 0 50px;
  &__inner {
    position: relative;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 15px;
    margin-top: 30px;
  }
  &__item {
    background: #F2F2F2;
    border-radius: 25px;
    padding: 20px;
    &-title {
      font-size: 20px;
      line-height: 150%;
      letter-spacing: -0.4px;
    }
  }
  &__subitems {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 7px 14px;
    margin-top: 3px;
  }
  &__subitem {
    display: flex;
    align-items: center;
    gap: 4px;
    &-icon {
      max-width: 14px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 11px;
      line-height: 120%;
      letter-spacing: -0.22px;
      color: #888888;
    }
  }
  &-diagram {
    position: relative;
    max-width: 175px;
    margin: 20px auto 0;
    img {
      width: 100%;
    }
  }
  .percent {
    position: absolute;
    z-index: 2;
    border-radius: 7px;
    padding: 4px 8px;
    font-size: 11px;
    letter-spacing: -0.22px;
    color: #222222;
    span {
      color: #666666;
    }
  }
  &__percent {
    top: 28px;
    left: -68px;
  }
  &__percent2 {
    right: -50px;
    bottom: 28px;
  }
  &-statistics {
    margin-top: 20px;
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 14px;
    }
  }
  &-statistics__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    &-left {
      width: 80px;
      flex-shrink: 0;
    }
    &-right {
      max-width: 330px;
      width: 1000%;
      display: flex;
      align-items: center;
    }
    &-title {
      width: 80px;
      font-size: 14px;
      line-height: 150%;
      letter-spacing: -0.28px;
    }
  }
  &-statistics__percent {
    width: 40px;
    flex-shrink: 0;
    font-size: 11px;
    line-height: 150%;
    letter-spacing: -0.22px;
    margin-right: 5px;
  }
  &-statistics__line {
    position: relative;
    max-width: 220px;
    width: 100%;
    height: 8px;
    span:nth-child(1) {
      width: 100%;
      height: 100%;
      display: block;
      background: #CCCCCC;
      border-radius: 2px;
    }
    span:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background: #3A60EA;
      border-radius: 2px;
    }
  }
  &-statistics__indicators {
    width: 55px;
    flex-shrink: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 4px;
    margin-left: 5px;
    i {
      width: 7px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 11px;
      line-height: 150%;
      letter-spacing: -0.22px;
      color: #F71A1A;
    }
  }
  &__img {
    position: absolute;
    top: -50px;
    right: -80px;
    width: 97px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: -80px;
    bottom: -100px;
    width: 89px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.info {
  padding: 50px 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 40px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 10000px;
      height: 100%;
      background: linear-gradient(180.00deg, rgb(80, 114, 238),rgb(58, 96, 234) 100%);
      border-radius: 50px 0 0 50px;
      pointer-events: none;
    }
  }
  &__img {
    position: relative;
    z-index: 2;
    width: 576px;
    display: flex;
    align-items: flex-end;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 370px;
      height: 370px;
      background: #fff;
      border-radius: 50%;
      filter: blur(295px);
      opacity: 0.78;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: calc(100% - 616px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    padding: 32px 24px 32px 0;
  }
  &__title {
    color: #fff;
  }
  &__items {
    max-width: 550px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
    gap: 8px;
    margin-top: 40px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 8px;
    &-icon {
      width: 40px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 160%;
      color: #CDD7FA;
    }
  }
}
.advantages {
  padding: 50px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #F2F2F2;
    border-radius: 20px;
    padding: 45px 15px;
    &-icon {
      max-width: 100%;
      margin-bottom: auto;
      img {
        max-width: 100%;
      }
    }
    &-text {
      flex-grow: 1;
      display: flex;
      align-items: flex-end;
      font-size: 24px;
      line-height: 150%;
      font-weight: 500;
      letter-spacing: -0.48px;
      margin-top: 24px;
    }
  }
}
.traffic {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 35px;
    padding: 32px 24px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 10000px;
      height: 100%;
      background: #F2F2F2;
      pointer-events: none;
    }
  }
  &__img-wrap {
    position: relative;
    width: 50%;
    display: flex;
    align-items: flex-end;
    background: linear-gradient(180.00deg, rgb(80, 114, 238),rgb(58, 96, 234) 100%);
    border-radius: 0 50px 50px 0;
    overflow: hidden;
  }
  &__img {
    img {
      max-width: 100%;
      margin-left: 20px;
      margin-bottom: -120px;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    top: 50px;
    right: 38px;
    width: 47px;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    z-index: 1;
    left: 35px;
    bottom: 120px;
    width: 34px;
    img {
      width: 100%;
    }
  }
  &__title {
    position: relative;
    z-index: 2;
  }
  &-social {
    position: relative;
    z-index: 2;
    width: calc(100% + 128px);
    background: #fff;
    border-radius: 16px;
    padding: 12px;
  }
  &-statistics {
    margin-top: 12px;
  }
  &-statistics__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &-statistics__item {
    position: relative;
    height: 30px;
    &:last-child {
      height: auto;
    }
    &:after {
      content: '';
      position: absolute;
      top: 6px;
      right: 0;
      width: calc(100% - 30px);
      height: 0.5px;
      background: #DDDDDD;
      pointer-events: none;
    }
    &-title {
      position: relative;
      width: 30px;
      span {
        display: block;
        font-size: 8px;
        line-height: 150%;
        letter-spacing: -0.16px;
      }
    }
  }
  &-statistics__socials {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    padding-left: 44px;
    margin-top: 8px;
  }
  &-statistics__social {
    position: relative;
    &-title {
      display: flex;
      align-items: center;
      gap: 5px;
      i {
        width: 13px;
        flex-grow: 0;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 8px;
        line-height: 150%;
        letter-spacing: -0.16px;
      }
    }
  }
  &-statistics__percent-group {
    position: absolute;
    top: -86px;
    left: 50%;
    transform: translate(-50%, 0);
    height: 72px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
  }
  &-statistics__percent {
    position: relative;
    z-index: 1;
    background: #fff;
    padding: 1px 2px;
    font-size: 8px;
    line-height: 150%;
    letter-spacing: -0.16px;
  }
  &-statistics__percent-diagram {
    width: 22px;
    background: #3A60EA;
    border-radius: 2px 2px 0 0;
  }
}
.policy {
  padding: 225px 0 100px;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    p {
      font-size: 18px;
      line-height: 188%;
    }
    strong {
      display: block;
    }
    a {
      display: inline-flex;
      align-items: center;
      gap: 10px;
      margin-left: 5px;
      i {
        width: 20px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 24px;
        font-weight: 500;
        color: #4C6FED;
      }
    }
    hr {
      width: 100%;
      height: 10px;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
  }
}

