.footer {
  background: #222222;
  padding: 60px 0;
  color: #fff;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__logo {
    width: 171px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__title {
    text-align: center;
    margin-top: 20px;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px 40px;
    margin-top: 40px;
  }
  &__btn {
    height: 69px;
    gap: 26px;
    background: #4C6FED;
    border-radius: 220px;
    padding: 5px 10px 5px 40px;
    &:hover {
      background: #1f46c9;
    }
    span {
      font-size: 20px;
      line-height: 150%;
      color: #fff;
      letter-spacing: -0.4px;
    }
    .arrow {
      width: 47px;
      height: 47px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EAEAEA;
      border-radius: 50%;
      svg {
        max-width: 90%;
        width: auto;
        height: auto;
      }
    }
  }
  &__email {
    display: flex;
    align-items: center;
    gap: 10px;
    i {
      width: 20px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 24px;
      line-height: 150%;
      font-weight: 500;
    }
  }
  &__policy {
    font-size: 16px;
    line-height: 150%;
    text-decoration: underline;
    text-underline-offset: 2px;
    color: #9E9E9E;
    margin-top: 40px;
    &:hover {
      color: #fff;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding: 40px 0;
    &__group {
      gap: 20px;
      margin-top: 20px;
    }
    &__btn {
      height: 60px;
      gap: 10px;
      padding: 5px 10px;
      span {
        font-size: 16px;
        letter-spacing: -0.2px;
      }
      .arrow {
        width: 36px;
        height: 36px;
      }
    }
    &__email {
      span {
        font-size: 16px;
      }
    }
    &__policy {
      margin-top: 20px;
    }
  }
}